// src/pages/AdminReportsPage.js

import React, { useEffect, useState } from 'react';
import { BACKEND_URL } from '../services/api'; // Ajusta si lo importas distinto

export default function AdminReportsPage() {
  const [reportDate, setReportDate] = useState('');
  const [cashierFilter, setCashierFilter] = useState('all');
  const [cashiers, setCashiers] = useState([]);
  const [reportContent, setReportContent] = useState('');
  const [showExcelButton, setShowExcelButton] = useState(false);

  useEffect(() => {
    loadCashiersForFilter();
  }, []);

  // Cargar cajeros para dropdown
  const loadCashiersForFilter = async () => {
    try {
      const resp = await fetch(`${BACKEND_URL}/users?limit=99999&offset=0&order=username:asc`);
      const users = await resp.json();
      // Filtra solamente cajeros
      const cashiersOnly = users.filter(u => u.role === 'cashier');
      setCashiers(cashiersOnly);
    } catch (error) {
      console.error("Error cargando cajeros:", error);
    }
  };

  // Generar reporte
  const generateReport = async () => {
    if (!reportDate) {
      alert("Seleccione una fecha");
      return;
    }
    try {
      const respSales = await fetch(`${BACKEND_URL}/sales?limit=99999&offset=0&order=date:asc`);
      let sales = await respSales.json();
      // Filtra por fecha (YYYY-MM-DD):
      sales = sales.filter(s => s.date.slice(0,10) === reportDate);

      if (cashierFilter !== "all") {
        sales = sales.filter(s => s.cashierId === cashierFilter);
      }

      // Traemos todos los usuarios para mostrar nombre de cajero
      const respUsers = await fetch(`${BACKEND_URL}/users?limit=99999&offset=0&order=username:asc`);
      const users = await respUsers.json();

      let totalDaySales = 0;
      let totalStockUsed = 0;

      let html = `<div class="report-section">
                    <h4>Ventas ${cashierFilter === "all" ? "Totales" : "del Cajero"}</h4>`;
      if (sales.length === 0) {
        html += `<p>No hay ventas en esta fecha.</p>`;
      } else {
        sales.forEach(sale => {
          totalDaySales += sale.total || 0;
          let cashier = users.find(u => u.id === sale.cashierId);
          html += `<div class="mb-3">
                    <p>
                      <strong>Venta ID:</strong> ${sale.id || "(sin ID)"}
                      | <strong>Cajero:</strong> ${cashier ? cashier.username : 'Desconocido'}
                      | <strong>Hora:</strong> ${new Date(sale.date).toLocaleTimeString()}
                      | <strong>Total:</strong> $${(sale.total || 0).toFixed(2)}
                    </p>
                    <p><strong>Forma de pago:</strong> ${sale.paymentMethod || 'N/A'}</p>
                    <table class="table table-sm">
                      <thead>
                        <tr>
                          <th>Producto</th>
                          <th>Cantidad</th>
                          <th>Precio</th>
                          <th>Subtotal</th>
                        </tr>
                      </thead>
                      <tbody>`;

          if (sale.items && Array.isArray(sale.items)) {
            sale.items.forEach(item => {
              totalStockUsed += item.quantity || 0;
              html += `<tr>
                        <td>${item.productName}</td>
                        <td>${item.quantity}</td>
                        <td>$${(item.price || 0).toFixed(2)}</td>
                        <td>$${(item.price * item.quantity).toFixed(2)}</td>
                      </tr>`;
            });
          }
          html += `</tbody></table></div>`;
        });

        html += `<p><strong>Total del Día:</strong> $${totalDaySales.toFixed(2)}</p>`;
        html += `<p><strong>Total de Stock Consumido:</strong> ${totalStockUsed} unidades</p>`;
      }
      html += `</div>`;

      // Balance de caja (no implementado en tu ejemplo)
      html += `<div class="report-section">
                 <h4>Balance de Caja</h4>
                 <p>No hay información de caja en tiempo real.</p>
               </div>`;

      // Stock actual de productos
      const respProds = await fetch(`${BACKEND_URL}/products?limit=99999&offset=0&order=name:asc`);
      const products = await respProds.json();
      html += `<div class="report-section">
                 <h4>Stock Actual</h4>
                 <table class="table table-bordered">
                   <thead>
                     <tr>
                       <th>Producto</th>
                       <th>Stock</th>
                     </tr>
                   </thead>
                   <tbody>`;
      products.forEach(prod => {
        html += `<tr>
                   <td>${prod.name}</td>
                   <td>${prod.stock}</td>
                 </tr>`;
      });
      html += `</tbody></table></div>`;

      setReportContent(html);
      setShowExcelButton(true);
    } catch (error) {
      console.error("Error generando reporte:", error);
      setReportContent("<div class='text-danger'>Error generando reporte.</div>");
      setShowExcelButton(false);
    }
  };

  // Exportar a Excel
  const exportReportToExcel = async () => {
    if (!reportDate) return;

    try {
      const respSales = await fetch(`${BACKEND_URL}/sales?limit=99999&offset=0&order=date:asc`);
      let sales = await respSales.json();
      sales = sales.filter(s => s.date.slice(0,10) === reportDate);

      if (cashierFilter !== "all") {
        sales = sales.filter(s => s.cashierId === cashierFilter);
      }
      const respUsers = await fetch(`${BACKEND_URL}/users?limit=99999&offset=0&order=username:asc`);
      const users = await respUsers.json();

      const respProducts = await fetch(`${BACKEND_URL}/products?limit=99999&offset=0&order=name:asc`);
      const allProducts = await respProducts.json();

      // Construir arrays para Excel
      const ventasData = [];
      ventasData.push(["Venta ID", "Cajero", "Hora", "Total", "Tipo de Pago", "Items y Stock"]);

      sales.forEach(sale => {
        const cashier = users.find(u => u.id === sale.cashierId);
        const itemsInfo = sale.items.map(item => {
          // Optional: ver stock final del producto
          const product = allProducts.find(p => p.id === item.productId);
          const finalStock = product ? product.stock : "N/A";
          return `${item.productName} x${item.quantity} (Stock: ${finalStock})`;
        }).join("; ");

        ventasData.push([
          sale.id || "(sin ID)",
          cashier ? cashier.username : "Desconocido",
          new Date(sale.date).toLocaleTimeString(),
          (sale.total || 0).toFixed(2),
          sale.paymentMethod || "N/A",
          itemsInfo
        ]);
      });

      // Balance de caja (no implementado)
      const cajaData = [["(Sin datos de caja implementados)"]];

      // Stock actual
      const stockData = [];
      stockData.push(["Producto", "Stock"]);
      allProducts.forEach(prod => {
        stockData.push([prod.name, prod.stock]);
      });

      // Crear workbook
      const wb = window.XLSX.utils.book_new();
      const wsVentas = window.XLSX.utils.aoa_to_sheet(ventasData);
      window.XLSX.utils.book_append_sheet(wb, wsVentas, "Ventas");
      const wsCaja = window.XLSX.utils.aoa_to_sheet(cajaData);
      window.XLSX.utils.book_append_sheet(wb, wsCaja, "Caja");
      const wsStock = window.XLSX.utils.aoa_to_sheet(stockData);
      window.XLSX.utils.book_append_sheet(wb, wsStock, "Stock");

      const filename = `Reporte_${reportDate}_${cashierFilter === "all" ? "Todos" : cashierFilter}.xlsx`;
      window.XLSX.writeFile(wb, filename);
    } catch (error) {
      console.error("Error exportando a Excel:", error);
      alert("No se pudo exportar el reporte a Excel.");
    }
  };

  return (
    <div>
      <h3><i className="fa-solid fa-chart-line"></i> Reportes de Ventas Diarias</h3>
      <div className="form-row mb-3">
        <div className="form-group col-md-4">
          <label>Seleccionar Fecha</label>
          <input
            type="date"
            className="form-control"
            value={reportDate}
            onChange={e => setReportDate(e.target.value)}
          />
        </div>
        <div className="form-group col-md-4">
          <label>Filtrar por Cajero</label>
          <select
            className="form-control"
            value={cashierFilter}
            onChange={e => setCashierFilter(e.target.value)}
          >
            <option value="all">Todos</option>
            {cashiers.map(c => (
              <option key={c.id} value={c.id}>
                {c.username}
              </option>
            ))}
          </select>
        </div>
        <div className="form-group col-md-4 align-self-end">
          <button className="btn btn-primary btn-custom" onClick={generateReport}>
            <i className="fa-solid fa-file-alt"></i> Generar Reporte
          </button>
        </div>
      </div>

      {/* Reporte HTML */}
      <div dangerouslySetInnerHTML={{ __html: reportContent }}></div>

      {/* Botón de exportar a Excel */}
      {showExcelButton && (
        <button className="btn btn-secondary btn-custom" onClick={exportReportToExcel}>
          <i className="fa-solid fa-file-export"></i> Exportar a Excel
        </button>
      )}
    </div>
  );
}
